.date {
	width: 1.375rem;
	height: 1.375rem;
	line-height: 1.375rem;
	border-radius: 9999px;
	display: grid;
	place-content: center;
	font-size: 0.7rem;
	cursor: pointer;
	user-select: none;
	color: var(--ec-gray-9);
}

	.date:hover {
		background-color: var(--ec-gray-1);
	}

	.date[data-dimmed='true'] {
		color: var(--ec-gray-5);
	}

	.date[data-active='true'] {
		color: #fff;
		background-color: #20c997;
	}

	.date[data-active='true']:hover {
			background-color: #12b886;
		}

	.date[data-today='true'] {
		color: #fff;
		background-color: #228be6;
	}
