.overflowCard {
	width: 13rem;
	background: #fff;
	padding: 0.5rem 0.5rem 0.625rem 0.5rem;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
	border: 1px solid #dee2e6;
	border-radius: 0.5rem;
	overflow: hidden;
	position: relative;
	display: flex;
	flex-direction: column;
}

[data-ec-color-scheme='dark'] .overflowCard {
	background: #3a3a3a;
	border: 1px solid #424242;
}

@media (prefers-color-scheme: dark) {
	[data-ec-color-scheme='auto'] .overflowCard {
		background: #3a3a3a;
		border: 1px solid #424242;
	}
}

.eventsWrapper {
	display: flex;
	flex-direction: column;
	gap: 0.125rem;
	max-height: min(308px, 90vh);
	overflow-y: auto;
}

.label {
	color: var(--ec-gray-9);
	text-align: center;
	font-size: 0.875rem;
	font-weight: 600;
	margin-bottom: 0.5rem;
}

.empty {
	text-align: center;
	font-size: 0.75rem;
	color: var(--ec-gray-7);
}
